function Logo({changeLogo, loaded}){

    const classes = LogoStyle();  

    return(
        <AnimatePresence>
            {loaded && 
                (<div className={classes.logo__container}>
                    <motion.div initial={{opacity:0}}
                    animate={{opacity: changeLogo, transition: { duration: 2.5 },}}>
                        <img className={classes.background} src={BackgroundHeader}/>
                    </motion.div>
                    <motion.div layoutId="box" transition={{duration: 2}}
                    className={`box ${changeLogo ? classes.logo__header : classes.logo__loading}`}>
                        <LogoAnimated/>
                    </motion.div>
                </div>)
            }
        </AnimatePresence>
    );
}

export default Logo;